<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/bwpractitioner/' + queries.practitioner)">
            <v-icon light left>mdi-close-circle-outline</v-icon>
            <span>{{ $t(`App.hardcoded-texts.Cancel`) }}</span>
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-btn :disabled="!decision" class="success darken-1" dark @click="process()">
            <v-icon light left>mdi-content-save</v-icon>
            <span>{{ $t(`App.hardcoded-texts.Save`) }}</span>
          </v-btn>
        </v-list-item>
        <v-divider color="white"></v-divider>          
      </v-list>
    </v-navigation-drawer>
    <v-card 
      class="mx-auto"
      max-width="700"
      outlined
    >
      <v-card-title class="primary darken-1 white--text text-uppercase font-weight-bold">
        Exam Exemption Request
      </v-card-title>
      <v-card-text class="my-3">
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Current Status:</span>
          </v-col>
          <v-col cols="8">{{ currentStatus }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Request Date:</span>
          </v-col>
          <v-col cols="8">{{ date }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Request Comments:</span>
          </v-col>
          <v-col cols="8">{{ comment }}</v-col>
        </v-row>
        <v-divider/>
        <v-row class="justify-space-between">
          <v-col cols="4">
            <span class="font-weight-bold">Decision (Approve/Reject):</span>
          </v-col>
          <v-col cols="8">
            <v-autocomplete
              label="Decision" 
              v-model="decision" 
              :items="decisions"
              outlined 
              hide-details="auto" 
              item-text="display"
              item-value="code"
              dense
              >
            </v-autocomplete>
          </v-col>
        </v-row>
        <v-divider/>
      </v-card-text>
    </v-card>
  </v-container>
</template>
<script>
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      currentStatus: '',
      date: '',
      comment: '',
      decisions: [{
        display: "Approve",
        code: 'approve'
      }, {
        display: "Reject",
        code: "reject"
      }],
      decision: '',
    }
  },
  methods: {
    getExemption() {
      fetch("/fhir/Basic/" + this.queries.exemption).then((response) => {
        response.json().then(async(results) => {
          let exemptStatusCoding = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-exemption-status"
          }).valueCoding
          this.currentStatus = exemptStatusCoding.display
          if(!this.currentStatus) {
            this.currentStatus = await this.$fhirutils.codeLookup(exemptStatusCoding.system, exemptStatusCoding.code)
          }
          this.date = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-exemption-date"
          }).valueDate
          this.comment = results.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-exemption-comment"
          })?.valueString
        })
      })
    },
    process() {
      this.overlay = true;
      fetch("/fhir/Basic/" + this.queries.exemption).then((response) => {
        response.json().then(async(exemption) => {
          let index = exemption.extension.findIndex((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/bhpc-exemption-status"
          })
          let status = {
            url: "http://ihris.org/fhir/StructureDefinition/bhpc-exemption-status",
            valueCoding: {
              system: "http://ihris.org/fhir/CodeSystem",
              code: "pending"
            }
          }
          if(this.decision === 'reject') {
            status.valueCoding.code = 'rejected'
          } else if(this.decision === 'approve') {
            status.valueCoding.code = 'active'
          }
          exemption.extension[index] = status
          let opts = {
            method: "PUT",
            headers: {
              "Content-Type": "application/fhir+json"
            },
            redirect: 'manual',
            body: JSON.stringify(exemption)
          }
          fetch("/fhir/Basic/" + exemption.id, opts).then((response) => {
            this.overlay = false;
            if(response.status === 200) {
              this.$store.commit("setMessage", {
                type: "success",
                text: "Decision Saved",
              });
              this.$router.push('/resource/view/bwpractitioner/' + this.queries.practitioner)
            } else {
              this.$store.commit("setMessage", {
                type: "error",
                text: "Failed to save decision."
              });
            }
          }).catch(() => {
            this.overlay = false;
            this.$store.commit("setMessage", {
              type: "error",
              text: "Failed to save decision."
            });
          })
        })
      })
    }
  },
  created() {
    this.getExemption()
  }
}
</script>